

#createLabel {
  font-weight: 700;
  font-size: 1.1rem;
}

#createInput {
  display: block;
  width: 100%;
  padding: 8px 4px;
  margin-top: 0.3rem;
  background-color: rgba(74, 74, 74, 0.61);
  color: white;
  font-size: 1rem;
  border: 0;
  border-radius: 4px;
}

#createBtn {
  margin: 1rem 0;
  width: 100%;
  height: 30px;
  background-color: rgb(128, 28, 222);
  color: white;
  font-size: 1.1rem;
  font-weight: 700;
  border: 0;
  border-radius: 4px;
  cursor: pointer;
}

#createBtn:hover {
  background-color: rgb(144, 47, 235);
}

@media only screen and (min-width: 38em) {
 
  #createBtn {
    height: 60px;
  }

} 
